.page-list {
  max-width: 1024px;
  background: radial-gradient(circle, rgba(231,185,129,1) 0%, rgba(224,159,79,1) 24%, rgba(170,120,58,1) 78%, rgb(137 93 38) 100%);
  box-shadow: 6px 0px 0px 11px rgb(146 101 46), 8px 2px 20px 11px rgb(0 0 0);
  padding: 10px;
  margin: 30px;
  width: 100%;
}

.list {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: flex-start;
          align-items: flex-start;
  background: rgb(123,154,143);
  background: radial-gradient(circle, rgba(60,143,114) 0%, rgba(0,112,71,1) 19%, rgba(0,31,19,1) 86%, rgba(0,0,0,1) 100%);
  overflow-y: scroll;
  height: 430px;
  border-radius: 15px;
  border: 4mm ridge darkolivegreen;
  padding: 20px 10px 20px 30px;
  width: 512px;
  max-width: 90%;
