}
/* ::-webkit-scrollbar {
  display: none;
} */

.list__item  {
  margin: 0.5rem 0;
  text-align: justify;
  display: -webkit-flex;
  display: flex;
	-webkit-justify-content: center;
	        justify-content: center;
	-webkit-flex-wrap: wrap;
	        flex-wrap: wrap;
}

.list__item_auto-type :last-child {
  font-family: sans-serif;
  font-size: 25px;
  letter-spacing: 5px;
  overflow: hidden;
  background: linear-gradient(90deg, #000, #000, #000);
  background-repeat: no-repeat;
