html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed, 
figure, figcaption, footer, header, hgroup, 
menu, nav, output, ruby, section, summary,
time, mark, audio, video {
	margin: 0;
	padding: 0;
	border: 0;
	font-size: 100%;
	font: inherit;
	vertical-align: baseline;
}
/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure, 
footer, header, hgroup, menu, nav, section {
	display: block;
}
body {
	line-height: 1;
}
ol, ul {
	list-style: none;
}
blockquote, q {
	quotes: none;
}
blockquote:before, blockquote:after,
q:before, q:after {
	content: '';
	content: none;
}
table {
	border-collapse: collapse;
	border-spacing: 0;
}

body {
  background: rgb(231,185,129);
  background: radial-gradient(circle, rgb(255 213 163) 0%, rgb(230 181 121) 24%, rgb(179 131 72) 78%, rgb(137 93 38) 100%);
  overflow-y: scroll;
}

.App {
  text-align: center;
  overflow: scroll;
  background: rgb(231,185,129);
  background: radial-gradient(circle, rgb(255 213 163) 0%, rgb(230 181 121) 24%, rgb(179 131 72) 78%, rgb(137 93 38) 100%);
}
.app-swipe {
  -webkit-transform: perspective(900px) rotate3d(180,0,0,20deg);
          transform: perspective(900px) rotate3d(180,0,0,20deg);
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: row;
          flex-direction: row;
  max-width: 725px;
  overflow-x: hidden;
  margin: 10px auto;
  margin-bottom: 45px;
  border-radius: 15px;
  background: rgb(231,185,129);
  background: radial-gradient(circle, rgb(52 43 32) 0%, rgb(143 113 78) 78%, rgb(156 126 90) 100%);
  box-shadow: 6px 0px 0px 11px rgb(125 101 72), 8px 2px 20px 11px rgb(0 0 0);
}
.swipe {
  display: -webkit-flex;
  display: flex;
}

.swipe-right {
  -webkit-transform: translate(0px);
          transform: translate(0px);
}

.swipe-left {
  -webkit-transform: translate(-1024px);
          transform: translate(-1024px);
  margin: auto 130px;
  -webkit-align-items: center;
          align-items: center;
}

@media screen and (min-width: 720px) {
  .App {
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
    height: 1024px;
    height: 100vh;
  }
  
}

@media screen and (max-width: 920px) {
  .app-swipe, .swipe {
    max-width: 600px;
  }
}
